export function getMergeSortAnimations(array) {
    const animations = [];
    if (array.length <= 1) return array;
    const auxiliaryArray = array.slice();
    mergeSortHelper(array, 0, array.length - 1, auxiliaryArray, animations);
    return animations;
  }
  
  function mergeSortHelper(
    mainArray,
    startIdx,
    endIdx,
    auxiliaryArray,
    animations,
  ) {
    if (startIdx === endIdx) return;
    const middleIdx = Math.floor((startIdx + endIdx) / 2);
    mergeSortHelper(auxiliaryArray, startIdx, middleIdx, mainArray, animations);
    mergeSortHelper(auxiliaryArray, middleIdx + 1, endIdx, mainArray, animations);
    doMerge(mainArray, startIdx, middleIdx, endIdx, auxiliaryArray, animations);
  }
  
  function doMerge(
    mainArray,
    startIdx,
    middleIdx,
    endIdx,
    auxiliaryArray,
    animations,
  ) {
    let k = startIdx;
    let i = startIdx;
    let j = middleIdx + 1;
    while (i <= middleIdx && j <= endIdx) {
      // These are the values that we're comparing; we push them once
      // to change their color.
      animations.push([i, j]);
      // These are the values that we're comparing; we push them a second
      // time to revert their color.
      animations.push([i, j]);
      if (auxiliaryArray[i] <= auxiliaryArray[j]) {
        // We overwrite the value at index k in the original array with the
        // value at index i in the auxiliary array.
        animations.push([k, auxiliaryArray[i]]);
        mainArray[k++] = auxiliaryArray[i++];
      } else {
        // We overwrite the value at index k in the original array with the
        // value at index j in the auxiliary array.
        animations.push([k, auxiliaryArray[j]]);
        mainArray[k++] = auxiliaryArray[j++];
      }
    }
    while (i <= middleIdx) {
      // These are the values that we're comparing; we push them once
      // to change their color.
      animations.push([i, i]);
      // These are the values that we're comparing; we push them a second
      // time to revert their color.
      animations.push([i, i]);
      // We overwrite the value at index k in the original array with the
      // value at index i in the auxiliary array.
      animations.push([k, auxiliaryArray[i]]);
      mainArray[k++] = auxiliaryArray[i++];
    }
    while (j <= endIdx) {
      // These are the values that we're comparing; we push them once
      // to change their color.
      animations.push([j, j]);
      // These are the values that we're comparing; we push them a second
      // time to revert their color.
      animations.push([j, j]);
      // We overwrite the value at index k in the original array with the
      // value at index j in the auxiliary array.
      animations.push([k, auxiliaryArray[j]]);
      mainArray[k++] = auxiliaryArray[j++];
    }
  }

  export function getBubbleSort(array)
  {
    let animations = [];
    let auxArray = array.slice();
    bubbleSort(auxArray, animations);
    array = auxArray;
     return [animations, array];
  }

  function bubbleSort(auxArray, animations)
  {
    const n = auxArray.length;
    for(let i = 0; i < n - 1; i++)
    {
      for(let j = 0; j < n - i - 1; j ++)
      {
        animations.push([j,j+1]); //We change colour twice to show the line moveing over
        animations.push([j,j+1]);
        if(auxArray[j] > auxArray[j+1]) //if j is bigger in value than j++ we swap them
        {
          animations.push([j, auxArray[j+1]]);//We show we are swapping the colours
          animations.push([j+1, auxArray[j]]);
          swap(auxArray, j, j+1);
        }else{
          animations.push([-1, -1]);
          animations.push([-1, -1]);
        }
      }
    }

  }

  export function getQuickSort(array) {
    const animations = [];
    quickSortHelper(array, 0, array.length - 1, animations);
    // return array;
    return animations;
  }
  //-1 pivot
  //-2 swap
  //-3 traversing the arrays with left and right indices
  function quickSortHelper(array, startIndex, endIndex, animations) {
    if (startIndex >= endIndex) return;
    // animations.push(-1);
    animations.push(-2);
    const pivot = startIndex;
    let leftIndex = startIndex + 1;
    let rightIndex = endIndex;
    //zero value in last to show that we are changing pivot
    animations.push([pivot, leftIndex, rightIndex, 0]);
    while (rightIndex >= leftIndex) {
      // animations.push(-3);
      //optional pushes to delay the animation effect
      // animations.push([leftIndex, rightIndex]);
      animations.push([leftIndex, rightIndex]);
      animations.push([leftIndex, rightIndex]);
      if (array[leftIndex] > array[pivot] && array[rightIndex] < array[pivot]) {
        animations.push(-2);
        animations.push([
          leftIndex,
          rightIndex,
          array[leftIndex],
          array[rightIndex]
        ]);
        swap(array, leftIndex, rightIndex);
      }
      if (array[leftIndex] <= array[pivot]) leftIndex++;
      if (array[rightIndex] >= array[pivot]) rightIndex--;
    }
    animations.push(-2);
    animations.push([pivot, rightIndex, array[pivot], array[rightIndex]]);
    swap( array, pivot, rightIndex);
    const leftSubArrayIsSmaller =
      rightIndex - 1 - startIndex < endIndex - (rightIndex + 1);
    if (leftSubArrayIsSmaller) {
      quickSortHelper(array, startIndex, rightIndex - 1, animations);
      quickSortHelper(array, rightIndex + 1, endIndex, animations);
    } else {
      quickSortHelper(array, rightIndex + 1, endIndex, animations);
      quickSortHelper(array, startIndex, rightIndex - 1, animations);
    }
  }
    
  function swap(auxArray, firstIDX, secondIDX)
  {
    let temp = auxArray[firstIDX];
    auxArray[firstIDX] = auxArray[secondIDX];
    auxArray[secondIDX] = temp;
  }




